.main_card_slider {
  width: calc(100% - 115px);
  position: relative;
  height: 100%;
  display: flex;
  align-items: center; }
  .main_card_slider .slick-slide {
    outline: none; }
  .main_card_slider .img-wrap img {
    width: 100%;
    height: 100%;
    object-fit: contain; }

.mini_slider {
  width: 90px;
  margin-right: 25px; }
  .mini_slider .img-wrap {
    width: 90px;
    height: 90px;
    border: 1px solid #242321;
    margin-bottom: 15px; }
    .mini_slider .img-wrap img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
  .mini_slider .slick-slide {
    transition: all .2s;
    text-align: center;
    cursor: pointer;
    outline: none; }

.slider_card-wrap {
  width: 100%;
  display: flex;
  position: relative;
  align-items: center; }

.slider_navigation {
  transition: all .2s;
  background: #FFFFFF;
  width: 42px;
  height: 42px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer; }
  .slider_navigation path {
    display: flex;
    transition: all .2s; }
  .slider_navigation__block {
    width: calc(100% - 90px);
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-between;
    top: 50%;
    transform: translateY(-50%);
    left: 103px;
    z-index: 9; }
  .slider_navigation:hover {
    background: #f28820; }
    .slider_navigation:hover svg path {
      fill: #fff; }

.commodity_section {
  position: relative;
  z-index: 99; }

.item_content__wrap {
  display: flex; }

.item_left_content__wrap {
  width: calc(100% - 640px); }

.item_description__block {
  position: relative;
  margin-left: 80px;
  width: 560px; }

.item_description_content {
  position: sticky;
  top: 150px; }

.item_description_title {
  position: relative; }

.item_description_type {
  margin: 10px 0; }

.item_description_category__block {
  margin-top: 5px;
  margin-bottom: 20px; }

.item_description_btn {
  margin-bottom: 10px;
  width: 100%; }

.item_description_fast {
  width: 100%; }

.item_select__block {
  width: calc(100% - 625px);
  padding-left: 34px; }
  .item_select__block .char_prof {
    width: 100% !important; }

.item_tab {
  margin-top: 100px; }
  .item_tab_content__block {
    position: relative;
    overflow: hidden; }
  .item_tab_content {
    position: relative;
    bottom: -100vh;
    transition: all 500ms ease-in-out 500ms;
    max-height: 0px;
    opacity: 0; }
    .item_tab_content_active {
      bottom: 0;
      opacity: 1;
      max-height: 5000px;
      padding-bottom: 40px;
      padding-top: 20px; }
  .item_tab_link__block {
    display: flex;
    align-items: center;
    width: 100%;
    border-bottom: 1.5px solid #242321; }
  .item_tab_link {
    text-align: center;
    cursor: pointer;
    padding: 0px 15px;
    padding-bottom: 10px;
    transition: all .2s;
    border-bottom: 2px solid transparent; }
    .item_tab_link_active {
      border-bottom: 2px solid #2b2a27; }
  .item_tab_info_prof {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px; }
  .item_tab_info_name {
    width: 350px; }
  .item_tab_info_value {
    width: 50px;
    text-align: right;
    margin-right: 35px; }

@media (max-width: 1310px) {
  .item_left_content__wrap {
    width: 540px;
    margin-right: 60px; }
  .item_description__block {
    margin-left: 0;
    width: calc(100% - 600px); } }

@media (max-width: 992px) {
  .item_description_btn__block {
    position: fixed;
    width: 100%;
    bottom: 0;
    left: 0;
    z-index: 999;
    background: #fff;
    padding: 16px; }
  .item_content__wrap {
    flex-direction: column-reverse; }
  .item_description__block {
    width: 100%; }
  .item_left_content__wrap {
    width: 560px;
    margin-right: 0;
    margin: 0 auto; }
  .slider_card-wrap {
    flex-direction: column-reverse; }
  .main_card_slider {
    width: 100%; }
  .slider_navigation__block {
    left: 0;
    width: 100%; }
  .item_tab {
    margin-top: 40px; }
  .mini_slider {
    margin-top: 25px;
    width: 100%;
    margin-right: 0; }
    .mini_slider .img-wrap {
      width: 90px !important;
      margin-bottom: 0px;
      margin-right: 15px; }
  .best_catalog_section {
    position: relative;
    margin-top: 30px;
    margin-bottom: 30px; }
  .item_description_content {
    position: relative;
    top: unset; } }

@media (max-width: 630px) {
  .item_left_content__wrap {
    width: 100%; }
  .item_tab_info_prof {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end; }
  .item_tab_info_name {
    width: 100%; }
  .item_tab_info_value {
    width: 100%;
    margin-right: 0; }
  .item_tab_content_active {
    padding-bottom: 10px; }
  .item_tab_link__block, .item_tab_content__block {
    width: 100%; } }

@media (max-width: 420px) {
  .item_description_btn__block {
    padding: 10px; }
    .item_description_btn__block .btn_standart {
      padding: 10px; } }

@media (max-width: 360px) {
  .mini_slider .img-wrap {
    width: 70px !important;
    height: 70px; } }
