.bread_crumbs_section {
  position: relative;
  margin-bottom: 30px; }

.bread_crumbs__block {
  display: flex;
  align-items: center; }

.bread_crumbs_slash {
  opacity: 0.5;
  margin: 0px 12px; }

.bread_crumbs_link {
  transition: all .2s;
  opacity: 0.7; }
  .bread_crumbs_link.active {
    opacity: 1; }
  .bread_crumbs_link:hover {
    color: #f28820;
    opacity: 1; }

@media (max-width: 480px) {
  .bread_crumbs_section {
    margin-bottom: 15px; } }
