.kredit__block {
  background-color: #efefef;
  padding: 13px 26px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 15px 0; }

.kredit_btn {
  text-transform: uppercase;
  margin-left: 15px;
  width: 202px; }

.item_comment__wrap .kredit__block {
  background-color: transparent;
  border: 1px solid #2b2a27;
  transition: all .2s; }

.item_comment__block {
  width: 100%; }

.none_comments_text {
  margin-top: 40px;
  position: relative;
  transition: all .2s;
  max-height: 1000px;
  transition-delay: 1s;
  left: 0; }
  .none_comments_text_hidden {
    max-height: 0px;
    left: -100vw; }

.kredit__block_none {
  background-color: #efefef !important;
  border: 1px solid #efefef !important; }

.comment_profile {
  padding: 15px 25px;
  background-color: #efefef;
  margin-bottom: 20px;
  position: relative;
  animation-name: anim_comment;
  animation-duration: 1s; }
  .comment_profile .form_stars {
    pointer-events: none; }

.comment_name {
  margin-right: 15px; }
  .comment_name__block {
    display: flex;
    align-items: center;
    margin-bottom: 5px; }

.comment_form.answer .answer_tel, .comment_form.answer .answer_mail, .comment_form.answer .answer_star {
  display: none; }

.comment_text_manager {
  transition: all .2s;
  display: flex;
  justify-content: flex-end;
  cursor: pointer; }
  .comment_text_manager:hover {
    color: #d6b212; }

.comment_answer__block .comment_profile {
  padding: 5px 20px; }

@keyframes anim_comment {
  0% {
    opacity: 0;
    left: -100vw; }
  100% {
    opacity: 1;
    left: 0; } }

.comment_form {
  width: 600px;
  padding: 25px !important; }
  .comment_form .footer_input__wrap {
    display: flex;
    flex-direction: column; }
  .comment_form .comment_name {
    margin-right: 0; }
  .comment_form .search_menu_title {
    margin-bottom: 35px; }
  .comment_form .user_send, .comment_form .footer_input {
    color: #242321;
    border-bottom: 1px solid #242321; }
  .comment_form .wrap_for_footer_btn {
    justify-content: flex-start; }
  .comment_form .footer_input {
    width: 70%; }
  .comment_form .comment_name__block {
    margin: 20px 0px;
    margin-bottom: 40px; }
  .comment_form .btn_standart {
    letter-spacing: 1px;
    width: 190px;
    margin-top: 20px; }

.comment_star {
  display: flex;
  align-items: center; }

@media (max-width: 992px) {
  .item_comment__block {
    width: 100%; } }

@media (max-width: 660px) {
  .kredit__block {
    flex-direction: column; }
    .kredit__block .kredit_btn {
      margin-top: 15px;
      width: 100%; } }

@media (max-width: 600px) {
  .comment_form .footer_input {
    width: 100%; } }

@media (max-width: 460px) {
  .comment_form .comment_name__block {
    flex-direction: column;
    align-items: end;
    margin: 0;
    margin-bottom: 35px; } }
